<template>
  <div style="padding: 10px">
    <div style="margin: 10px 0 ;display: flex">
      <!--      <el-button type="primary">导入</el-button>-->
      <!--      <el-button type="primary">导出</el-button>-->
      <el-button type="primary" @click="handleEdit2">新增产品</el-button>
    </div>
    <!--    <div style="margin: 10px 0 ;display: flex">-->
    <!--      <el-input v-model="search" placeholder="请输入RFID" style="width: 20%" clearable/>-->
    <!--      <el-button type="primary" style="margin-left: 5px">查询</el-button>-->
    <!--    </div>-->
    <el-table :data="tableData" stripe style="width: 100%" border>
      <el-table-column prop="id" label="ID" width="75px" sortable/>
      <el-table-column prop="name" label="产品名称" width="90px" />
      <el-table-column prop="company" label="公司名称" width="90px" />
      <el-table-column prop="partNumber" label="物料编号" width="90px" />
      <el-table-column prop="specifications" label="规格" width="90px" />
      <el-table-column prop="reinforcement" label="配筋" width="90px" />
      <el-table-column prop="abbreviationCode" label="简码号" width="90px" />
      <el-table-column prop="dateOfManufacture" label="生产日期" width="90px" />
      <el-table-column prop="shift" label="生产班次" width="90px" />
      <el-table-column prop="shiftLeader" label="生产班长" width="90px" />
      <el-table-column prop="examiner" label="质检员" width="90px" />
      <el-table-column prop="state" label="产品状态" width="90px" />
      <el-table-column fixed="right" label="操作" width="155px">
        <template #default="scope">
          <el-button type="text" size="small" @click="handleEdit(scope.row)"
          >编辑</el-button
          >
          <el-popconfirm title="确定删除吗?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button type="text" size="small">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :small="0"
          :disabled="false"
          :background="true"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
        v-model="dialogVisible"
        title="编辑"
        width="30%"
        @close="closeAddDialog"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="产品名称" style="width: 80%">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="公司名称" style="width: 80%">
          <el-input v-model="form.company" />
        </el-form-item>
        <el-form-item label="物料编号" style="width: 80%">
          <el-input v-model="form.partNumber" />
        </el-form-item>
        <el-form-item label="规格" style="width: 80%">
          <el-input v-model="form.specifications" />
        </el-form-item>
        <el-form-item label="配筋" style="width: 80%">
          <el-input v-model="form.reinforcement" />
        </el-form-item>
        <el-form-item label="简码号" style="width: 80%">
          <el-input v-model="form.abbreviationCode" />
        </el-form-item>
        <el-form-item label="生产日期" style="width: 80%">
          <el-input v-model="form.dateOfManufacture" />
        </el-form-item>
        <el-form-item label="生产班次" style="width: 80%">
          <el-input v-model="form.shift" />
        </el-form-item>
        <el-form-item label="生产班长" style="width: 80%">
          <el-input v-model="form.shiftLeader" />
        </el-form-item>
        <el-form-item label="质检员" style="width: 80%">
          <el-input v-model="form.examiner" />
        </el-form-item>
        <el-form-item label="产品状态" style="width: 80%">
          <el-input v-model="form.state" />
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeAddDialog">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
    <el-dialog
        v-model="dialogVisible2"
        title="新增产品"
        width="30%"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="产品名称" style="width: 80%">
          <el-input v-model="form.name" />
        </el-form-item>
        <el-form-item label="公司名称" style="width: 80%">
          <el-input v-model="form.company" />
        </el-form-item>
        <el-form-item label="物料编号" style="width: 80%">
          <el-input v-model="form.partNumber" />
        </el-form-item>
        <el-form-item label="规格" style="width: 80%">
          <el-input v-model="form.specifications" />
        </el-form-item>
        <el-form-item label="配筋" style="width: 80%">
          <el-input v-model="form.reinforcement" />
        </el-form-item>
        <el-form-item label="简码号" style="width: 80%">
          <el-input v-model="form.abbreviationCode" />
        </el-form-item>
        <el-form-item label="生产日期" style="width: 80%">
          <el-input v-model="form.dateOfManufacture" />
        </el-form-item>
        <el-form-item label="生产班次" style="width: 80%">
          <el-input v-model="form.shift" />
        </el-form-item>
        <el-form-item label="生产班长" style="width: 80%">
          <el-input v-model="form.shiftLeader" />
        </el-form-item>
        <el-form-item label="质检员" style="width: 80%">
          <el-input v-model="form.examiner" />
        </el-form-item>
        <el-form-item label="产品状态" style="width: 80%">
          <el-input v-model="form.state" />
        </el-form-item>
<!--        <el-form-item label="图片1" style="width: 80%">-->
<!--          <el-input v-model="form.companyImage" />-->
<!--        </el-form-item>-->
<!--        <el-form-item label="图片2" style="width: 80%">-->
<!--          <el-input v-model="form.shiftImage" />-->
<!--        </el-form-item>-->
<!--        <el-form-item label="图片3" style="width: 80%">-->
<!--          <el-input v-model="form.examinerImage" />-->
<!--        </el-form-item>-->
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "product",
  data(){
    return{
      form:{},
      dialogVisible:false,
      dialogVisible2:false,
      currentPage:1,
      pageSize:10,
      total:10,
      search:'',
      tableData:[]
    }
  },
  created() {
    this.load()
  },
  methods:{
    load(){
      request.post("/product/queryByLimit?pageNum="+this.currentPage+"&pageSize="+this.pageSize+"&search="+this.search
          // pageNum:this.currentPage,
          // pageSize:this.pageSize,
          // search:this.search
      ).then(res=>{
        this.tableData=res.data.records
        this.total=res.data.total
      })
    },
    add(){
      this.dialogVisible=true,
          this.form={}
    },
    //清除对话框中的数据
    closeAddDialog() {
      this.dialogVisible = false   //关闭对话框
      // this.$refs.addRoleForm.resetFields();
      this.form = this.$options.data().form
    },
    save(){
      if(this.form.id){
        request.put("/product",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.dialogVisible=false
        })
      }else{
        request.post("/product",this.form).then(res => {
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"新增成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.form = this.$options.data().form
          this.dialogVisible2=false
        })
      }
    },
    // eslint-disable-next-line no-unused-vars
    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleEdit2(){
      this.dialogVisible2 = true
    },
    handleDelete(id){
      request.delete("/product/"+id).then(res=>{
        if(res.code==="0"){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else {
          this.$message({
            type:"error",
            message:res.msg
          })
        }
        this.load()
      })
    },
    handleSizeChange(pageSize){
      this.pageSize=pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.currentPage=pageNum
      this.load()
    }
  }
}
</script>

